import React from 'react'
import ReactDOM, { hydrate } from 'react-dom'
import { BrowserRouter } from 'react-router-dom'
import App from './App'
import { resize } from './utils/util'

const rootElement = document.getElementById('root')

if (rootElement.hasChildNodes()) {
  hydrate(
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <App />
    </BrowserRouter>,
    rootElement,
  )
} else {
  ReactDOM.render(
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <App />
    </BrowserRouter>,
    document.getElementById('root'),
  )
}

//ë¦¬ì‚¬ì´ì¦ˆ ì´ë²¤íŠ¸ ë“±ë¡
let timer = null
let debounce = null
window.addEventListener('resize', function () {
  //ìŠ¤ë¡œí‹€ë§ êµ¬í˜„
  if (!timer) {
    timer = setTimeout(() => {
      timer = null
      resize()
    }, 100)
  }
})

//ìœˆë„ìš° ë¡œë“œ ì´ë²¤íŠ¸ ë“±ë¡.
let scroll = 0
window.addEventListener('load', function () {
  resize()
  const $header = document.querySelector('#headerWrapper')
  $header.style.top = '0px'

  //ë¡œë“œí›„ì— FABs ìˆ¨ê¸°ê¸°
  let $FABs
  setTimeout(() => {
    $FABs = document.querySelectorAll('.FAB')
    $FABs.forEach((i, idx) => {
      setTimeout(() => {
        i.style.right = '0px'
      }, 150 * (idx + 1))
    })
  }, 1000)

  //ìŠ¤í¬ë¡¤ ì´ë²¤íŠ¸ ë“±ë¡. ìŠ¤í¬ë¡¤ì´ ìœ„ë¡œ ì˜¬ë¼ê°€ë©´ í—¤ë” ë³´ì´ê³  ë‚´ë ¤ê°€ë©´ ìˆ¨ê¹€.
  document.body.addEventListener('scroll', function (event) {
    $FABs = document.querySelectorAll('.FAB')
    //ìŠ¤ë¡œí‹€ë§ êµ¬í˜„
    if (!timer) {
      timer = setTimeout(() => {
        timer = null
        //ì—¬ê¸°ì— ìŠ¤ë¡œí‹€ë§ ë‚´ë¶€ì—ì„œ í• ì¼ ì‘ì„±
        if (document.body.scrollTop >= scroll) {
          $header.style.top = '-48px'
        } else {
          $header.style.top = '0px'
        }
        scroll = document.body.scrollTop
        //ìŠ¤í¬ë¡¤í•˜ë©´ FABs ë³´ì´ê¸°
        $FABs.forEach((i, idx) => {
          setTimeout(() => {
            i.style.right = null
          }, 150 * idx)
        })
      }, 100)
    }
    //ë””ë°”ìš´ì‹±. ìŠ¤í¬ë¡¤ ëë‚˜ê³  1.5ì´ˆ í›„ì— FABs ìˆ¨ê¸°ê¸°
    if (debounce) {
      clearTimeout(debounce)
    }
    debounce = setTimeout(() => {
      $FABs.forEach((i, idx) => {
        setTimeout(() => {
          i.style.right = '0px'
        }, 150 * (idx + 1))
      })
    }, 1500)
  })
})
